import { createClient } from '@supabase/supabase-js';

const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL;
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY;

if (!supabaseUrl || !supabaseAnonKey) {
  throw new Error('Missing Supabase environment variables. Please check your .env file.');
}

console.log('Initializing Supabase client with:', {
  url: supabaseUrl,
  hasAnonKey: !!supabaseAnonKey
});

export const supabase = createClient(supabaseUrl, supabaseAnonKey, {
  auth: {
    autoRefreshToken: true,
    persistSession: true,
    detectSessionInUrl: true
  },
  db: {
    schema: 'public'
  }
});

// Test the connection
(async () => {
  try {
    console.log('Testing Supabase connection...');
    
    // Test industries table
    const { data: industries, error: industriesError } = await supabase
      .from('industries')
      .select('count');
    console.log('Industries count:', industries, 'Error:', industriesError);

    // Test solutions table
    const { data: solutions, error: solutionsError } = await supabase
      .from('solutions')
      .select('count');
    console.log('Solutions count:', solutions, 'Error:', solutionsError);

    // Test achievements table
    const { data: achievements, error: achievementsError } = await supabase
      .from('achievements')
      .select('count');
    console.log('Achievements count:', achievements, 'Error:', achievementsError);

  } catch (error) {
    console.error('Supabase connection test failed:', error);
  }
})(); 